html, body {
  height: 100%;
}

.button {
  width: 114px;
  height: 32px;
  box-sizing: border-box;
  border: 1px solid #00CFD7;
  background-color: #00D7B4;
  border-radius: 43px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 15px;
  color: #fff;
  font-weight: normal;
  line-height: 17px;
  cursor: pointer;
}

.button_inverse {
  background-color: transparent;
  border-color: #58626B;
  color: gray;
}

.footer {
  display: flex;
  min-height: 65px;
  align-items: center;
  padding: 7px 50px;
  position: relative;
  box-sizing: border-box;
  background-color: #fff;
}

.footer__logo {
  width: 174px;
  height: 50px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 20px;
  color: #000;
  font-weight: normal;
  line-height: 23px;
  background-color: #C4C4C4;
  position: absolute;
  cursor: pointer;
}

.footer__buttons {
  display: flex;
  justify-content: center;
  flex-basis: 100%;
}

.footer__item {
  margin-left: 15px;
}

.footer__item:first-child {
  margin-left: 0;
}

.footer__timer {
  width: 104px;
  height: 31px;
  border: 1px solid #00CFD7;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 18px;
  color: #00CFD7;
  font-weight: normal;
  line-height: 21px;
  border-radius: 15px;
}

.circle {
  width: 31px;
  height: 31px;
  background-color: #00CFD7;
  border: 1px solid #00CFD7;
  box-sizing: border-box;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.circle_disable {
  border-color: rgba(0, 0, 0, 0.5);
  background-color: transparent;
}

.circle_disable .circle__img > svg * {
  fill: rgba(0, 0, 0, 0.5);
}

.circle_red {
  background-color: #EF2F2F;
  border-color: #EF2F2F;
}

.circle__img {
  display: flex;
  justify-content: center;
  align-items: center;
}

.video-body {
  background-color: #EDE8E5;
  height: calc(100% - 65px);
  padding: 15px 45px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  align-content: flex-start;
  flex-wrap: wrap;
  overflow: scroll;
}

.video-body_timer {
  justify-content: center;
  align-content: center;
  height: 100%;
}

.video-body__item {
  margin: 15px 5px;
}

.video-card {
  width: 310px;
  height: 200px;
  box-sizing: border-box;
  position: relative;
  border-radius: 10px;
  border: 1px solid #000;
  cursor: pointer;
}

.video-card_middle {
  width: 780px;
  height: 400px;
}

.video-card_middle .video-card__name {
  width: 250px;
  height: 32px;
  left: 13px;
  bottom: 9px;
}

.video-card_middle .video-card__micro {
  top: 20px;
  right: 20px;
}

.video-card_big {
  width: 780px;
  height: 825px;
}

.video-card_big .video-card__name {
  width: 250px;
  height: 32px;
  left: 10px;
  bottom: 10px;
}

.video-card_big .video-card__micro {
  top: 20px;
  right: 20px;
}

.video-card_large {
  width: 950px;
  height: 620px;
}

.video-card_large .video-card__name {
  width: 300px;
  height: 36px;
  left: 10px;
  bottom: 10px;
}

.video-card_large .video-card__micro {
  top: 20px;
  right: 20px;
}

.video-card_super-large {
  width: 1356px;
  height: 825px;
}

.video-card_super-large .video-card__name {
  width: 300px;
  height: 36px;
  left: 10px;
  bottom: 10px;
}

.video-card_super-large .video-card__micro {
  top: 20px;
  right: 20px;
}

.video-card__video {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
}

.video-card__video img {
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  border-radius: 10px;
  margin: -2px 0px 0 -2px;
}

.video-card__name {
  position: absolute;
  bottom: 1px;
  left: 1px;
  width: calc(100% - 2px);
  height: 36px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 5px 10px;
  border-radius: 9px;
  background-color: #F7A321;
}

.video-card__img {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}

.video-card__name-text {
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 18px;
  color: rgba(0, 0, 0, 0.8);
  font-weight: normal;
  line-height: 21px;
}

.video-card__micro {
  position: absolute;
  top: 10px;
  right: 10px;
}

.container__modal {
  width: 100%;
  height: calc(100% - 65px);
  background-color: #EDE8E5;
  opacity: 0.8;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal {
  width: 580px;
  padding: 20px 28px 40px 42px;
  box-sizing: border-box;
  border-radius: 12px;
  background-color: #fff;
}

.modal__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal__info {
  display: flex;
  align-items: center;
}

.modal__info-img-wrap {
  margin-right: 15px;
  height: 51px;
  width: 51px;
  border: 2px solid #C4C4C4;
  box-sizing: border-box;
  border-radius: 26px;
  background-color: #fff;
  padding: 4px;
}

.modal__info-img-wrap_red {
  border-color: #EF2F2F;
}

.modal__info-img-wrap_red .modal__info-img {
  background-color: #EF2F2F;
}

.modal__info-img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background-color: #C4C4C4;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 30px;
  color: #fff;
  font-weight: bold;
  line-height: 20px;
}

.modal__info-text {
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 18px;
  color: #58626B;
  font-weight: bold;
  line-height: 21px;
}

.modal__timer {
  width: 77px;
  height: 32px;
  border: 1px solid #00CFD7;
  box-sizing: border-box;
  border-radius: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 18px;
  color: #00CFD7;
  font-weight: normal;
  line-height: 21px;
}

.modal__description {
  margin-top: 10px;
  margin-left: 66px;
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 17px;
  color: #58626B;
  font-weight: normal;
  line-height: 20px;
}

.modal__clients-list {
  margin-top: 22px;
  margin-left: 66px;
}

.clients-list__client {
  margin-bottom: 10px;
  display: flex;
}

.clients-list__client-img {
  width: 51px;
  height: 51px;
  margin-right: 10px;
}

.clients-list__client-img img {
  width: 100%;
  height: 100%;
}

.clients-list__client-desc {
  padding-top: 3px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.clients-list__client-name {
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 18px;
  color: #000;
  font-weight: bold;
  line-height: 21px;
}

.clients-list__client-rank {
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 13px;
  color: #58626B;
  font-weight: normal;
  line-height: 15px;
  text-transform: uppercase;
}

.clients-list__appear {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.clients-list__appear_red .clients-list__appear-text {
  color: #EF2F2F;
}

.clients-list__appear_red .clients-list__appear-button {
  background-color: #EF2F2F;
}

.clients-list__appear-text {
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 18px;
  color: #00CFD7;
  font-weight: normal;
  line-height: 21px;
}

.clients-list__appear-button {
  width: 19px;
  height: 19px;
  background-color: #00D7B4;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 15px;
}

.modal__buttons {
  display: flex;
  margin-left: 66px;
  margin-top: 25px;
}

.modal__button:first-child {
  margin-right: 30px;
}

.container__modal-end {
  position: fixed;
  bottom: 75px;
  right: 0;
  width: 354px;
  box-sizing: border-box;
  padding: 34px 23px;
  background-color: #fff;
  border-radius: 14px 0 0 14px;
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 22px;
  color: #58626B;
  font-weight: normal;
  line-height: 25px;
  text-align: center;
}

.timer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.timer__heading {
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 40px;
  color: #58626B;
  font-weight: bold;
  line-height: 46px;
}

.timer__time {
  margin-top: 50px;
  width: 300px;
  height: 300px;
  box-sizing: border-box;
  border-right: 150px solid transparent;
  border-top: 150px solid #F7A321;
  border-left: 150px solid #F7A321;
  border-bottom: 150px solid #F7A321;
  border-top-left-radius: 150px;
  border-top-right-radius: 150px;
  border-bottom-left-radius: 150px;
  border-bottom-right-radius: 150px;
  transform: rotate(-45deg);
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 4px 4px 16px rgba(0, 0, 0, 0.25);
}

.timer__text {
  transform: rotate(45deg);
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-size: 57px;
  color: #58626B;
  font-weight: bold;
  line-height: 65px;
}
